<template>
  <div>
    <headerBar :header-data="headerData" />
    <!-- 项目信息 begin -->
    <div class="fixed-project" @touchmove.prevent>
      <div class="list">
        <h3 class="limit-h1">
          {{ wybmListItem.xmmc }}
        </h3>
        <div class="info">
          <span class="label">
            {{ CGFS[wybmListItem.cgfs] }}
          </span>
          <span>
            <i class="iconfont iconshouji" />
            {{ wybmListItem.sj }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ wybmListItem.kbsj | formatTime }}
          </span>
        </div>
      </div>
    </div>
    <div v-if="pageStyles == 0">
      <van-list
        ref="dida_list"
        class="common-list"
      >
        <div
          v-for="(item, index) in homedata"
          :key="index"
        >
          <yqhMouldsList class="content" style="margin-top:10px;" :data-item="item" :wybm-list-item="wybmListItem" :fbxx="fbxx" show-more="false" />
        </div>
      </van-list>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
    <div class="common-space" />
  </div>
</template>

<script land="script" scoped>
import { mapState } from 'vuex'
import yqhMouldsList from '@/components/list/yqhMouldsList.vue'
import errorPage from '@/components/result-page/index'
import constants from '@/common/js/constants'
import headerBar from '@/components/header/headerBar.vue'

export default {
  name: 'Yqhlist',
  components: {
    yqhMouldsList,
    errorPage,
    headerBar
  },
  data() {
    return {
      fbxx: '',
      headerData: {
        title: '项目信息',
        leftUrl: 'Wybm',
        leftText: ' '
      },
      pageStyles: 0,
      ind: '', // 点击事件
      pageUrl: '',
      homedata: []

    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem
    })
  },
  created() {
    const _this = this
    this.$bus.$on('yqhChange', function() {
      _this.getggData()
    })
    this.getggData()
    this.CGFS = constants.CGFS
  },
  methods: {
    parentReceive() {
      this.getggData()
    },
    changeBgc: function(index) {
      this.ind = index
    },
    getggData() {
      this.$store.dispatch('gyswybm/getGysbmXmggListAction', {
        zzid: this.userInfo.zzid,
        xmid: this.wybmListItem.xmid
      }).then(res => {
        console.log(res)
        this.homedata = res
        if (this.homedata.length === 0) {
          this.pageStyles = 1
        } else {
          this.pageStyles = 0
        }
      }).catch(error => {
        // this.loading = false
        // this.pageStyles = 3
        this.pageStyles = 3
        console.log('error=======', error)
      })
    }

  }

}
</script>
<style lang="css" scoped>
  .nav-bar{box-shadow: none;}
</style>

